// src/components/SecretPage.js
import React, { useState, useEffect } from 'react';
import ReactMarkdown from 'react-markdown';
import { createClient } from '@supabase/supabase-js';

const supabase = createClient(
  process.env.REACT_APP_SUPABASE_URL,
  process.env.REACT_APP_SUPABASE_PUBLISHABLE_KEY
);

const BetaKeyPage = () => {

  const [betaKey, setBetaKey] = useState('');
  const [description, setDescription] = useState('');
  const [isAuthorized, setIsAuthorized] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const name = urlParams.get('name');
    const secretKey = urlParams.get('secret_key');

    if (!name || !secretKey) {
      setLoading(false);
      setError('URL');
      return;
    }

    // SupabaseRPC
    const fetchBetaKey = async () => {
      try {
        const { data, error } = await supabase.rpc('get_beta_key', {
          p_name: name,
          p_secret_key: secretKey
        });

        if (error) throw error;

        if (data && data.length > 0) {
          setBetaKey(data[0].beta_key);
          setDescription(data[0].description);
          setIsAuthorized(true);
        } else {
          setError('');
        }
      } catch (error) {
        console.error('Access denied:', error);
        setError('');
      } finally {
        setLoading(false);
      }
    };

    fetchBetaKey();
  }, []);

  const copyToClipboard = async () => {
    try {
      await navigator.clipboard.writeText(betaKey);
      alert('');
    } catch (err) {
      console.error(':', err);
      alert('');
    }
  };

  if (loading) {
    return (
      <div className="App">
        <header className="App-header">
          <div className="loading">
            <h2> ...</h2>
            <p></p>
          </div>
        </header>
      </div>
    );
  }

  if (!isAuthorized) {
    return (
      <div className="App">
        <header className="App-header">
          <div className="error-page">
            <h1> </h1>
            <p>{error || 'URL'}</p>
          </div>
        </header>
      </div>
    );
  }

  return (
    <div className="App">
      <header className="App-header">
        <div className="secret-page">
					<h1></h1>


          <div className="description">
  					<ReactMarkdown>{description}</ReactMarkdown>
					</div>

          <div className="instructions">
            <h3> </h3>
						<hr/>
            <ol style={{ textAlign: 'left', maxWidth: '400px' }}>
              <li>Steam</li>
              <li></li>
              <li></li>
              <li></li>
							<ol>
								<li>
									<code className="password">{betaKey}</code>
									<button onClick={copyToClipboard} className="copy-button">
										 
									</button>
								</li>
							</ol>
              <li></li>
            </ol>
          </div>

          <div className="warning">
            <p> URL</p>
          </div>
        </div>
      </header>
    </div>
  );
};

export default BetaKeyPage;